.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.lightmode{
    background: darkcyan;
}

.darkmode{
    background: rgb(36, 36, 36)
}
body{
    padding:0;
    margin: 0 auto;
}
.night_btn{
    margin-right:15px;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
}
.navbar {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    width: 100%;
    display: grid;
    grid-gap: 10px;
    background: rgb(42, 44, 39);
    grid-template-columns: 0fr 1fr;
}
.navbar a{
    text-decoration: none;
}
.navbar .buttons a {
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
}
.navbar .buttons a:hover, .red{
    background: red;
}
.navbar h3{
    margin-left: 15px;
    white-space: nowrap;
    color: rgb(200, 238, 127);
}
.navbar .buttons{
    display: inline-flex;
}
.homeMenu{
    /* min-height: 100vw; */
    overflow: hidden;
}
::-webkit-scrollbar{
    display: none;
}
::-webkit-scrollbar {
    width: 6px;
    display: unset;
}

.darkmode ::-webkit-scrollbar-track{
    background: #222;
}
::-webkit-scrollbar-track {
    background: rgb(139, 139, 139);
    border-radius: 12px;
}
.darkmode ::-webkit-scrollbar-thumb{
    background-color: #555;
    border: 3px solid #666;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(71, 71, 71);
    border: 3px solid rgb(71, 71, 71);
    border-radius: 12px;
}

::-webkit-scrollbar-corner { background: #222}
.playerDropdown{
    display: contents;
}
.show{
    display: grid !important;
    grid-template-columns: 0fr 0fr;
    grid-gap: 10px;
    position: absolute;
}

.ban, .kick {
    width: 55px;
    /* background-color: red; */
    color: white;
    border: none;
    border-radius: 3px;
    height: 24px;
}
.ban, .kick{
    background-color: violet
}
.playerDropdown button:hover {
    background-color: rgb(189, 0, 189)
}
.playerMenu {
    height: 25px;
    padding-top: 10px;
    margin-top:10px;
    padding-bottom: 8px;
    display: none;
    background-color: #3e3e3e;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding-left: 10px;
    padding-right: 14px;
    border-radius: 8px;
}

.darkmode .playersOnline {
    background: rgb(46, 46, 46);
}

.darkmode .playersOnline h3 {
    color: rgb(157, 192, 75);
}

.darkmode .playersOnline ul {
    color: rgb(194, 194, 194);
}
.lightmode .playersOnline{
    background: black;
}


.playersOnline {
    margin-top: 20px;

    display: grid;
    grid-template-rows: 0fr 1fr;
    width: 300px;
    border: 1px solid rgb(56, 56, 56);
    border-radius: 12px;
    padding: 10px;
    height: 320px;
}

.playersOnline h3 {
    color: black;
    margin: 0 auto;
    text-align: center;
}

.playersOnline ul {
    overflow-y: auto;
    list-style: none;
    padding: 0;
    padding-left: 15px;
}

.playersOnline ul div li{
    margin-bottom: 8px;
}

.playersOnline ul li::before {
    content: "\2022";
    /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: greenyellow;
    /* Change the color */
    font-weight: bold;
    /* If you want it to be bold */
    display: inline-block;
    /* Needed to add space between the bullet and the text */
    width: 1em;
    /* Also needed for space (tweak if needed) */
    margin-left: -1em;
    /* Also needed for space (tweak if needed) */
}

.playersOnline hr {
    border: none;
    width: 100%;
    height: 1px;
    background: #838383;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

